<template>
  <div class="pop-up-window">
    <div class="container pop-up-inner">
      <div class="row">
        <div
          class="col selection-option"
          :class="{ active: user.id === selectedProfileId }"
          v-for="user in userList"
          :key="user.id"
        >
          <selectable-user
            :user-name="user.name"
            @select="setActiveUser(user)"
          ></selectable-user>
        </div>
        <div class="col selection-option">
          <new-user></new-user>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SelectableUser from "@/components/user/SelectableUser.vue";
import NewUser from "@/components/user/NewUser.vue";
import { useStore } from "vuex";
import { onBeforeMount, ref } from "vue";

export default {
  name: "UserSelection",
  components: { NewUser, SelectableUser },
  emits: ["userSelected"],
  setup(props, { emit }) {
    const store = useStore();
    const userList = ref(store.getters.userProfiles);
    const selectedProfileId = ref(store.getters.userProfileId);
    onBeforeMount(async () => {
      if (userList.value.length === 0) {
        await store.dispatch("fetchUserProfiles");
        userList.value = store.getters.userProfiles;
      }
    });
    const setActiveUser = async (user) => {
      store.commit("setActiveUser", user);
      await store.dispatch("fetchUserStats");
      emit("userSelected");
    };
    return {
      userList,
      selectedProfileId,
      setActiveUser,
    };
  },
};
</script>

<style scoped>
.pop-up-window {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}
.pop-up-inner {
  background-color: white;
  padding: 32px;
}

.selection-option {
}

.selection-option:hover {
  background-color: #f0f0f0; /* Hintergrundfarbe im Hover-Zustand */
}

.active {
  border-color: #42b983;
}
</style>
